import React, { Component } from 'react';
// import GoogleMapReact from 'google-map-react';

// const AnyReactComponent = ({ text }) => <div>{text}</div>;

class SimpleMap extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: props.data
    }
  }

  static defaultProps = {
    center: {
      lat: 59.95,
      lng: 30.33
    },
    zoom: 11
  };

  render() {
    return (
      // Important! Always set the container height explicitly
      <div id="where" style={{
        padding: '5em',
        height: 'auto',
        width: '100%',
        display: "flex",
        flexDirection:"column",
        justifyContent: 'center',
        alignItems:"center",
        backgroundColor:"#f0f0f0"
      }}>
        
        <p> {this.props.data ? this.props.data.metro : "loading"} </p>
        <p> {this.props.data ? this.props.data.buses : "loading"}  </p>
        
        <iframe
          title="google-map"
          scrolling="no"
          marginHeight="0"
          marginWidth="0"
          id="gmap_canvas"
          src="https://maps.google.com/maps?width=520&amp;height=400&amp;hl=en&amp;q=Passeig%20Valldaura%20218-220%20Barcelona+()&amp;t=&amp;z=12&amp;ie=UTF8&amp;iwloc=B&amp;output=embed"
          width="1000"
          height="500"
          frameBorder="0"
        ></iframe>
        <script
          type='text/javascript'
          src='https://embedmaps.com/google-maps-authorization/script.js?id=e22ef28f25381a0a04afc2cac95622cb5332b29a'
        ></script>

        
      </div>
    );
  }
}

export default SimpleMap;